.custom-carousel .owl-controls .owl-prev {
  position: absolute;
  width: 20px;
  height: 20px;
  top: -25px;
  right: 27px;
  -webkit-transition: all linear 0.2s;
  transition: all linear 0.2s;
  border-radius: 3px;
  background: #dddddd;
}
.custom-carousel .owl-controls .owl-prev:before {
  color: #fff;
  content: "\F104";
  font-family: fontawesome;
  font-size: 13px;
  left: 7px;
    position: absolute;
  top: 2px;
}
.custom-carousel .owl-controls .owl-next {
  position: absolute;
  width: 20px;
  height: 20px;
  top: -25px;
  right: 0px;
  -webkit-transition: all linear 0.2s;
  transition: all linear 0.2s;
  border-radius: 3px;
  background: #dddddd;
}
.custom-carousel .owl-controls .owl-next:before {
  content: "\F105";
